import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import { useFormContext } from 'react-hook-form';
import AppTextInput from '../search/AppTextInput';
import AppCheckbox from './AppCheckbox';

export default function AddressForm() {

  const {control, formState} = useFormContext();

  return (
    <>
      <Typography variant="h6" gutterBottom>
        Shipping address
      </Typography>   
      <Grid container spacing={3}>
        <Grid item xs={12} sm={6}>
          <AppTextInput control={control} name='firstName' label="First Name" />
        </Grid>
        <Grid item xs={12} sm={6}>
          <AppTextInput control={control} name='lastName' label="Last Name" />
        </Grid>
        <Grid item xs={12}>
          <AppTextInput control={control} name='address1' label="Address Line 1" />
        </Grid>
        <Grid item xs={12}>
          <AppTextInput control={control} name='address2' label="Address Line 2" />
        </Grid>
        <Grid item xs={12} sm={6}>
          <AppTextInput control={control} name='city' label="City" />
        </Grid>
        <Grid item xs={12} sm={6}>
        <AppTextInput control={control} name='state' label="State" />
        </Grid>
        <Grid item xs={12} sm={6}>
          <AppTextInput control={control} name='zip' label="Zip Code" />
        </Grid>
        <Grid item xs={12} sm={6}>
          <AppTextInput control={control} name='country' label="Country" />
        </Grid>
        <Grid item xs={12}>
          <AppCheckbox name='saveAddress' label='Save this as the default address' control={control} disabled={!formState.isDirty} />
        </Grid>
      </Grid>
    </>
  );
}